import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Navbar, Nav } from 'react-bootstrap';
import { GoogleLogin, GoogleLogout } from 'react-google-login';

export default class SundaraNavbar extends Component {
    constructor(){
      super()
      this.state = {loggedIn: false, fullName: "", showResults: false}
    }
    loginSuccess = (response) => {
      this.setState({loggedIn: true, fullName: response.profileObj.givenName});
    }
    loginFailure = () => {
      this.setState({loginFailed: true})
    }
    logout = () => {
      this.setState({loggedIn: false, fullName: ""})
    }
    render() {
      return (
        <div>
        <Navbar bg="dark" variant="dark">
        <Navbar.Brand href="/">
          Sundara
        </Navbar.Brand>
        <Nav className="mr-auto">
            <Nav.Link href="/#/">Home</Nav.Link>
        </Nav>
        <Nav className="mr-auto">
            <Nav.Link href="/#/privacy">Privacy</Nav.Link>
        </Nav>
        <Navbar.Collapse className="justify-content-end">
          {this.state.loggedIn ? <Navbar.Text>
            Hello, {this.state.fullName}!
            &emsp;
          </Navbar.Text> : null }
          <Navbar.Text>
          {this.state.loggedIn ? 
          <GoogleLogout
            clientId="4672322705-3o7hifd831mjo6qdj6mhm4qqtt9snh2p.apps.googleusercontent.com"
            buttonText="Logout"
            onLogoutSuccess={this.logout}
          /> : 
          <GoogleLogin
            clientId="4672322705-3o7hifd831mjo6qdj6mhm4qqtt9snh2p.apps.googleusercontent.com"
            buttonText="Login"
            onSuccess={this.loginSuccess}
            onFailure={this.loginFailure}
            cookiePolicy={'single_host_origin'}
            style={{display: "inline", height: "1vh"}}
          />
          }
          </Navbar.Text>
        </Navbar.Collapse>
      </Navbar>
      </div>
      );
    }
}